.Footer {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  background-color: rgb(22, 22, 22);
  color: white;
  font-family: "Nunito Sans", sans-serif;
  height: 300px;
}

.logo {
  font-size: 18px;
  color: floralwhite;
  font-weight: 1000;
  font-family: "Mulish";
  margin: 18px 39px 0px;
  cursor: pointer;
}

.OurContacts {
  font-size: 13px;
  margin-left: 40px;
}

.Desc {
  display: flex;
  justify-content: center;
  text-align: center;
  font-size: 14px;
  line-height: 1.5;
}

.Icons {
  display: flex;
  justify-content: right;
  margin-right: 2%;
}

.numbers {
  cursor: pointer;
  margin-right: 12px;
  width: 25px;
}/*# sourceMappingURL=Footer.module.css.map */